.codes-body {
	margin-bottom: 10px;
}

.code {
	display: flex;
	padding-bottom: 40px;
	width: 400px;
}

.code-icon {
	border-radius: 10%;
	box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.25);
	height: 30px;
	outline: 6px solid white;
	width: 30px;
}

.code-name {
	color: var(--secondary-color);
	font-size: 15px;
	font-weight: 700;
	margin-top: -3px;
	padding-left: 20px;
}

.code-development-type {
	color: var(--secondary-color);
	font-size: 12px;
	padding: 22px 0 0 50px;
	position: absolute;
}

.code-type {
	color: var(--tertiary-color);
	font-size: 12px;
	padding: 42px 0 0 50px;
	position: absolute;
}
